import React, { useState, useCallback } from 'react';
import { Plus, FileText, Search, Upload, X } from 'lucide-react';
import { useProductSearch } from '../hooks/useProductSearch';
import { useSupplierSearch } from '../hooks/useSupplierSearch';
import { useSavedItems } from '../hooks/useSavedItems';
import { productsIndex } from '../lib/meilisearch';
import LoadingSpinner from '../components/LoadingSpinner';
import toast from 'react-hot-toast';
import { isBrowser } from '../lib/isomorphic-helpers';

// Dynamic imports for PDF functionality
const PDFComponents = React.lazy(() => 
  import('@react-pdf/renderer').then(module => ({
    default: {
      PDFDownloadLink: module.PDFDownloadLink,
      Document: module.Document,
      Page: module.Page,
      Text: module.Text,
      View: module.View,
      StyleSheet: module.StyleSheet,
      Font: module.Font
    }
  }))
);

interface RFQFormData {
  companyName: string;
  email: string;
  address: string;
  city: string;
  zipCode: string;
  countryRegion: string;
  stateProvince: string;
  vendorName: string;
  vendorId: string | null;
  items: Array<{
    id: string;
    name: string;
    quantity: string;
    url: string;
    moq: string;
  }>;
  notes: string;
}

interface SearchResult {
  id: string;
  name: string;
  price?: string;
  image?: string;
  country?: string;
  category?: string;
  supplier?: string;
  marketplace?: string;
  moq?: string;
}

const initialFormData: RFQFormData = {
  companyName: '',
  email: '',
  address: '',
  city: '',
  zipCode: '',
  countryRegion: '',
  stateProvince: '',
  vendorName: '',
  vendorId: null,
  items: [],
  notes: ''
};

